}
.switcher {
    width: 35px;
    height: 20px;
    border-radius: 20px;
    background-color: #ff4c00;
    position: relative; 
}

.switcher:before {
    content: '';
    display: block;
    position: absolute;
    top: 50%;
    right: 2px;
    transform: translateY(-50%);
    height: 16px;
    width: 16px;
    border-radius: 50%;
    background-color: #ffffff;
    transition: all .3s ease-out;
}

.active.switcher:before {
    right: 17px;
    background-color: #fff;
}
.role-list {
    width: 100%;
    min-width: 350px;
    display: flex;
    flex-direction: column;
    background-color: #ffffff;
    border: 1px solid #dddddd;
    border-bottom-left-radius: 8px;
    border-bottom-right-radius: 8px;
}

.role-list a {
    text-decoration: none;
}

.role-list__item:hover {
    cursor: pointer;
    background-color: #f5f5f5;
}

.role-list__inner {
    margin: auto 0 auto auto;
}

.role-list__item {
    width: 100%;
}

.role-list__item {
    width: 100%;
    display: flex;
    padding: 15px 40px;
}

a:last-child .role-list__item {
    border-top: 1px solid #dddddd;
    padding: 15px 0;
    margin: 0 40px;
    width: calc(100% - 80px);
}

.role-list__icon {
    width: 26px;
    height: 26px;
    margin-right: 17px;
    margin-right: 17px;
}

.role-list__text {
    color: #000000;
    font-size: 14px;
    max-width: 160px;
    display: inline-block;
    padding-top: 3px;
}
.role {
    height: 56px;
    border: none;
    outline: none;
    list-style: none;
    position: relative;
}

.role__item {
    height: 56px;
    padding: 0 20px;
}

.role__item:hover {
    background-color: #f5f5f5;
}

.role__inner {
    position: absolute;
    top: 100%;
    right: -23px;
    -webkit-animation: appearence .8s ease-in;
            animation: appearence .8s ease-in;
    z-index: 4;
}

@-webkit-keyframes appearence {
    0% {
        transform: translateY(-100%);
    }

    100% {
        transform: translateY(0);
    }
}

@keyframes appearence {
    0% {
        transform: translateY(-100%);
    }

    100% {
        transform: translateY(0);
    }
}
.logo__img:last-child {
