}
.arrow {
    width: 24px;
    transition: all .4s ease;
}

.arrow {
    transform: rotateX(180deg);
}

.arrow.rotate {
    transform: rotateX(0);
}
.time-board__time {
    padding: 0 20px;
}
.header {
    width: 100%;
    height: 56px;
    background-color: #ffffff;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.header__inner {
    width: 100%;
    height: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}

.header__item:first-child {
    margin-right: auto;
}

.header__item:nth-child(3) {
    border-left: 1px solid #dddddd;
    border-right: 1px solid #dddddd;
    padding: 0 20px;
    height: 100%;
    display: flex;
    align-items: center;
}

