.header__item:last-child {
    cursor: pointer;
}
.button {
    background-color: #ff4c00;
    border: none;
    border-radius: 6px;
    padding: 10px 40px;
    color: #ffffff;
    min-width: 120px;
}
.list-item-info {
    background-color: #303030;
    border-radius: 8px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    min-width: 350px;
    position: relative;
}

.list-item-info:before {
    content: '';
    position: absolute;
    top: 50%;
    left: -5px;
    background-color: #303030;
    width: 10px;
    height: 10px;
    transform: translateY(-50%) rotate(45deg);
    z-index: 2;
}

.list-item-info__footer {
    display: flex;
    justify-content: space-between;
    align-items: center;
    border-top: 1px solid #ffffff;
}

.list-item-info__item {
    padding: 15px;
}

.list-item-info__footer {
    padding: 10px 15px;
}


.list-item-info__item .list-item-info__text {
    margin: 15px 0 0 0;
}

.list-item-info__footer .list-item-info__text {
    color: #dddddd;
}
.list-item {
    position: relative;
    list-style: none;
}

.list-item:hover .list-item__inner {
    display: flex;
}

.list-item__item {
    width: 190px;
    height: 36px;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    transition: all .3s linear;
    text-decoration: none;
    color: #000000;
    padding: 0 0 0 5px;
    -webkit-user-select: none;
}

.list-item__item:hover {
    cursor: pointer;
    border-radius: 8px;
    background-color: #ff4c00;
    color: #ffffff;
}

.list-item__item:hover .list-item__icon > svg:not(.white) path {
    fill: #ffffff;
}

.list-item__item:hover .list-item__icon > .white path {
    stroke: #ffffff;
}

.list-item__inner {
    display: none;
    margin-left: auto;
    height: 100%;
    align-items: center;
    padding: 0 9px 0 0;
}

.list-item__info {
    display: none;
    position: absolute;
    left: 100%;
    top: 50%;
    transform: translateY(-50%);
    z-index: 4;
    padding: 0 0 0 17px;
}

.list-item__icon {
    padding: 0 15px 0 0;
}

.list-item__inner:hover .list-item__info {
    display: block;
}

.img__img {
