.events-item-lg__buttons {
    width: 100%;
    display: flex;
    justify-content: space-between;
    padding: 15px 0 0 0;
}    

.events-item-lg__text,
.events-item-lg__title {
    padding-top: 10px;
}   

.events-item-lg__title {
    font-size: 16px;
    font-weight: 700;
    line-height: 1.4;
}

.events-item-lg__time {
    padding: 10px 0 0 0;
}

.events-item-lg__text,
.events-item-lg__time {
    color: #959595;
}

.events-item-lg__block-time {
    display: none;
    height: 24px;
    min-width: 86px;
    color: #ff3c00;
    font-size: 12px;
    border-radius: 4px;
    border: 1px solid #dddddd;    
    text-align: center;
    line-height: 1.8;
    position: absolute;
    top: -13px;
    left: 20px;
    padding: 0 14px;
    background-color: #ffffff;
    z-index: 2;
}

.events-item-lg.active .events-item-lg__block-time {  
    display: block;
}

.events-item-lg.new .events-item-lg__block-time {
    border: 1px solid #ff3c00;
    display: block;
}
.events-list .empty-message {
    margin-top: 20px;
}

.error-indicator {
    width: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
    color: #000000;
    margin-top: 100px;
    font-size: 18px;
}

.error-indicator span {
    margin: 5px;
}
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    width: 100%;
    height: 100%;
    background-color: #e5e5e5;
    font-family: 'Lato', sans-serif;
    font-size: 14px;
    font-weight: 500;
}

.app {
    width: 100%;
}

.main {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    display: flex;
    padding: 30px 0;
}

.content {
    display: flex;
    align-self: flex-start;
    flex-wrap: wrap;
    justify-content: space-between;
}
